<template>
  <div>
    <el-container class="container-main">
      <!--  -->
      <el-aside width="250px" class="main-right">
        <el-header class="header-title"></el-header>
        <draggable
          :list="CommonUserComponents"
          v-bind="{
            group: {
              name: 'people',
              pull: 'clone',
              put: false,
            },
            sort: false,
            ghostClass: 'ghostClass',
          }"
          :move="onMove"
          class="draggable-container"
        >
          <el-tag
            v-for="(item, index) in CommonUserComponents"
            :key="index"
            effect="dark"
            style="width: 100%"
          >
            <i :class="item.icon"></i> {{ item.name }}
          </el-tag>
        </draggable>
      </el-aside>
      <!--  -->
      <el-container>
        <el-header class="main-bottom header-padding">
          <!--  -->
          <el-tag
            effect="plain"
            type="danger"
            class="header-tags"
            @click="clearWidetConfit"
            ><i class="el-icon-delete"></i><span></span>
          </el-tag>
          <!-- json -->
          <el-tag
            effect="plain"
            type=""
            class="header-tags"
            @click="setFormJson"
            ><i class="el-icon-connection"></i><span>JSON</span>
          </el-tag>
          <!-- JSON -->
          <el-tag
            type=""
            effect="plain"
            class="header-tags"
            @click="saveWidetConfit"
            ><i class="el-icon-document"></i><span>JSON</span>
          </el-tag>
          <!--  -->
          <el-tag effect="plain" type="" class="header-tags" @click="showDialog"
            ><i class="el-icon-help"></i><span></span>
          </el-tag>
          <!-- DEMO -->
          <el-tag type="" @click="hrefDemo" effect="plain" class="header-tags"
            ><i class="el-icon-star-off"></i><span>DEMO</span>
          </el-tag>
        </el-header>
        <el-main class="box-padding">
          <div class="widget-form-container">
            <!--  -->
            <!-- sync -->
            <widgeform
              ref="configFormData"
              :configFormData="configFormData"
              :select.sync="widgetFormSelect"
            ></widgeform>
          </div>
        </el-main>
      </el-container>
      <!--  -->
      <el-aside width="250px" class="main-left">
        <el-tabs v-model="activeName" style="margin-top: -4px">
          <el-tab-pane label="" name="first">
            <widgetconfig :eleItem.sync="widgetFormSelect"></widgetconfig>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <formconfig :formdata="configFormData.config"></formconfig>
          </el-tab-pane>
        </el-tabs>
      </el-aside>
    </el-container>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="70%"
      top="5vh"
    >
      <dialogform ref="dialogForm"></dialogform>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="getDialogForm"></el-button>
      </span>
    </el-dialog>
    <!-- json -->
    <el-dialog :title="dialogTitle" :visible.sync="jsonDialog" top="5vh">
      <div v-if="!jsonTextareaReadonly">
        JSONjson
      </div>
      <div style="max-height: 400px; overflow-y: scroll">
        <el-input
          type="textarea"
          autosize
          :readonly="jsonTextareaReadonly"
          v-model="jsonmsg"
        >
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer" v-if="!jsonTextareaReadonly">
        <el-button @click="jsonDialog = false"> </el-button>
        <el-button type="primary" @click="setJsonBtn"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import draggable from "vuedraggable";
import widgeform from "./widgetForm";
import widgetconfig from "./widgetConfig";
import dialogform from "./dialogForm";
import formconfig from "./formConfig";
import { copy as util_Copy } from "../util/clone";
import { CommonUserComponents } from "./componentsConfig";
export default {
  components: {
    draggable: draggable,
    widgeform: widgeform,
    widgetconfig: widgetconfig,
    dialogform: dialogform,
    formconfig: formconfig,
  },
  data() {
    return {
      // json
      CommonUserComponents,
      // 
      configFormData: {
        list: [],
        config: {
          labelWidth: 100,
          labelPosition: "right",
          size: "small",
        },
      },
      widgetFormSelect: {
        key: null,
        options: {
          placeholder: null,
        },
      },
      // 
      dialogVisible: false,
      // json
      jsonDialog: false,
      // json
      jsonmsg: "",
      // 
      dialogTitle: "JSON",
      // 
      activeName: "first",
      // ,
      jsonTextareaReadonly: true,
    };
  },
  methods: {
    // 
    onMove() {
      return true;
    },
    // 
    saveWidetConfit() {
      this.dialogTitle = "JSON";
      this.jsonTextareaReadonly = true;
      this.jsonDialog = true;
      this.jsonmsg = JSON.stringify(this.configFormData, null, 1);
    },
    // 
    clearWidetConfit() {
      this.configFormData = {
        list: [],
        config: {
          labelWidth: 100,
          labelPosition: "right",
          size: "small",
        },
      };
      this.widgetFormSelect = {
        key: null,
        options: {
          placeholder: null,
        },
      };
    },
    // 
    showDialog() {
      if (this.configFormData.list.length === 0) {
        this.$message("");
        return;
      }
      // 
      this.dialogVisible = true;
      // 
      setTimeout(() => {
        // 
        this.$refs.dialogForm.setFormVal(util_Copy(this.configFormData));
      });
    },
    // 
    getDialogForm() {
      // getFormVal
      this.$refs.dialogForm.getFormVal().then((data) => {
        this.dialogTitle = "";
        this.jsonTextareaReadonly = true;
        this.jsonDialog = true;
        this.jsonmsg = JSON.stringify(data, null, 1);
      });
    },
    // DEMO
    hrefDemo() {
      this.$router.push({ path: "/demo" });
    },
    // json
    setFormJson() {
      this.dialogTitle = "JSON";
      this.jsonTextareaReadonly = false;
      this.jsonDialog = true;
      this.jsonmsg = JSON.stringify(
        {
          list: [],
          config: {
            labelWidth: 100,
            labelPosition: "right",
            size: "small",
          },
        },
        null,
        1
      );
    },
    // json
    setJsonBtn() {
      try {       
        this.initJson(this.jsonmsg)
        this.jsonDialog = false;
      } catch (error) {
        this.$message("JSON");
      }
    },
    // jsonjsonjson
    initJson(strJson) {
      this.configFormData = JSON.parse(strJson);
      this.widgetFormSelect = this.configFormData.list[0];
    },
  },
};
</script>
<style>
.container-main {
  height: 100vh;
}
.header-title {
  width: 100%;
  height: 35px !important;
  line-height: 25px !important;
  padding: 0px !important;
  border-bottom: 1px solid #999999;
}
.main-right {
  border-right: 1px solid #999;
  padding: 10px;
}
.draggable-container {
  cursor: move;
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 25px;
  gap: 7px;
}

.main-bottom {
  border-bottom: 1px solid #999;
  height: 45px !important;
  line-height: 45px;
}
.main-left {
  border-left: 1px solid #999;
  padding: 10px;
}
.header-padding {
  position: relative;
  padding: 0px !important;
  text-align: right;
}
.box-padding {
  padding: 0px !important;
  position: relative;
  overflow-y: auto;
}
.widget-form-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ghostClass {
  border: 2px solid #409eff !important;
}
.header-tags {
  margin-right: 10px;
  cursor: pointer;
}
.header-tags:nth-child(1) {
  position: absolute;
  left: 10px;
  top: 12px;
}
</style>