import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from "../components/container.vue"
import Demo from "../components/formsDemos/demo.vue"


Vue.use(VueRouter)


const routes = [{
    path: '/',
    name: '',
    component: Home
}, {
    path: "/demo",
    name: "",
    component: Demo
}]


export default new VueRouter({
    routes
})